export default {
	data() {
		return {
			userInfo: {}
		};
	},
	onLoad() {
		this.userInfo = uni.getStorageSync('userInfo');
	},
	methods: {
		toAbout(){
			uni.navigateTo({
				url:'../about/about'
			})
		},
		out() {
			uni.showModal({
			    title: '提示',
			    content: '确定要退出登录',
			    success: function (res) {
			        if (res.confirm) {
						uni.removeStorageSync('userInfo');
						uni.reLaunch({
							url: '../login/login'
						});
			        }
			    }
			});
		}
	}
};
